body{
	background: #8989ba;
background: -moz-linear-gradient(left, #8989ba 0%, #8989ba 100%);
background: -webkit-linear-gradient(left, #8989ba 0%,#8989ba 100%);
background: linear-gradient(to right, #8989ba 0%,#8989ba 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#8989ba', endColorstr='#8989ba',GradientType=1 );
}

.wrapper{
	width: 1000px;
	height: 600px;
	margin: 100px auto;
	background: #352a2a;
	display: flex;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	align-content: center;
	align-items: center;
	border: 1px solid black;
	border-radius: 4%;
	box-shadow: 0 0 10px #4b4766;
}
.wrap{
	width: 50%;	
	height: 80%;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-flow: column wrap;
	align-items: center;
	justify-content: center;
	align-content: space-evenly;
	border-left: 1px solid #efedfc;
	color:#efedfc;
	font-size: 25px;
}