.form{
	height: 60px;
	width: 500px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-evenly;
	align-items: center;
}
.btn{
	height: 60%;
	width: 25%;
	background-color: #524c75;
	color:#fff;
	border: none;
	outline: none;

}
.input{
	height: 40px;
	width: 60%;
	border: none;
	border-radius: 3%;
	outline: none;
	padding-left: 10px
}